import React, { Component } from 'react'
import {Field, reduxForm} from 'redux-form'


class EduForm extends Component {
    renderError({ error, touched }) {
        if (touched && error) {
          return (
            <div className="ui error message">
              <div className="header">{error}</div>
            </div>
          );
        }
      }

    renderInput=({input,label,meta})=>{
        const className = `field ${meta.error && meta.touched ? 'error' : ''}`;
        return (
            <div className={className}>
                <label>{label}</label>
                <input {...input} autoComplete='off'/>
                {this.renderError(meta)}
            </div>
        )
    }
    onSubmit= formValues => {
        this.props.onSubmit(formValues)
    }

    render() {
        
        return (
            
            <form onSubmit={this.props.handleSubmit(this.onSubmit)} className='ui form error'>
               <Field name='course' component={this.renderInput} label='Course Name'/> 
               <Field name='school' component={this.renderInput} label='Organisation Name' /> 
               <Field name='duration' component={this.renderInput} label='Duration' /> 
               <Field name='date' component={this.renderInput} label='Acuired Date' /> 
               <button className='ui button primary'>Submit</button>
            </form>
        )
    }
}

const validate = formValues => {
    const errors = {};
  
    if (!formValues.title) {
      errors.title = 'You must enter a title';
    }
  
    if (!formValues.desc) {
      errors.desc = 'You must enter a description';
    }
  
    return errors;
  };
  

export default  reduxForm({
    form: 'eduForm',validate
}) (EduForm)

